.lsection {
	background-color: $green3;
	padding: 16px 0 46px;
	&__title {
		margin-bottom: 40px;
		font-weight: 700;
		font-size:  58px;
		text-align: center;
		span {
			font-size: 69px;
			color: $green;
			position: relative;
			top: 5px; } }
	&__inner {
		max-width: 980px;
		display: flex;
		justify-content: space-between;
		margin: 0 auto;
		align-items: center; }
	&__text {
		font-size: 26px;
		line-height: 1.4; } }
